.modalWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  background: #0000003b;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  opacity: 0;

  &__active {
    opacity: 1;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 100;
  }
}

